.Contact {
  padding: 7rem;
  overflow: hidden;
}
.contact-container {
  position: relative;
  left: 10rem;
  width: 60%;
  display: grid;
  grid-template-columns: 40% 58%;
  gap: 12%;
}

/*========================================= left ====================*/

.contact-container .left-contact {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.contact-container .left-contact .social-link {
  background-color: var(--color-grey-5);
  color: var(--color-white);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--trans1);
}
.contact-container .left-contact .social-link a {
  text-decoration: none;
  margin: 0.7rem;
  display: inline-block;
  font-size: 1rem;
  color: var(--gray);
}
.contact-container .left-contact .social-link:hover {
  background: transparent;
  border-color: var(--trans1);
}
.contact-container .left-contact .social-link .social-icon {
  font-size: 1.5rem;
  margin-bottom: 01.5rem;
}
/*========================================= right ====================*/

.contact-container .right-contact {
  display: flex;
  justify-content: center;
  position: relative;
  flex: 1;
}
form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  position: relative;
  top: 0.5rem;
}
form span {
  padding-top: 3rem;
  color: var(--color-white);
}

.right-contact .user {
  width: 30rem;
  height: 3rem;
  padding: 1em;
  outline: none;
  border: 2px solid var(--color-grey-5);
  border-radius: 8px;
  font-size: 16px;
  background: transparent;
  color: var(--color-white);
  resize: none;
}

textarea {
  height: 8rem !important;
}
@media screen and (max-width: 480px) {
  .Contact {
    margin-top: 20rem;
    width: 200%;
  }

  .contact-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  .contact-container .right-contact {
    position: relative;
    top: 6rem;
    right: 3rem;
  }
  .contact-container .left-contact {
    position: relative;
    top: 6rem;
    right: 4rem;
  }
  form span{
    position: relative;
    bottom:2rem;
  }
}
