.qualification {
  padding: 5rem 14rem;
}

/*Timeline*/
.timeline {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}
.timeline .timeline-item {
  position: relative;
  padding-left: 3rem;
  border-left: 1px solid var(--color-grey-5);
}
.timeline .timeline-item .tl-icon {
  position: absolute;
  left: -27px;
  top: 0;
  background-color: var(--color-secondary);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.timeline .timeline-item .tl-icon i {
  font-size: 1.3rem;
}
.timeline .timeline-item .tl-duration {
  padding: 0.2rem 0.6rem;
  background-color: var(--color-grey-5);
  border-radius: 15px;
  display: inline-block;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 500;
}
.timeline .timeline-item h5 {
  padding: 1rem 0;
  font-size: 1.3rem;
  font-weight: 600;
  color: var(--color-grey0);
}
.timeline .timeline-item h5 span {
  color: var(--color-grey-2);
  font-weight: 500;
  font-size: 1.2rem;
}
.timeline .timeline-item p {
  color: var(--color-grey-3);
}

.right-about .btn-con {
  position: relative;
  top: 1rem;
  left: 18rem;
}
.main-title {
  left: 2rem;
  bottom: 5rem;
  font-size: 4rem;
  color: #ddd;
  text-align: center;
  font-weight: 700;
}
.main-title .temp {
  font-size: 1rem;
  color: var(--gray);
}
@media screen and (max-width: 480px) {
  .qualification {
    display: flex;
    flex-direction: column;
    gap: 5rem;
  }
  .qualification {
    position: relative;
    right: 3rem;
    bottom: 11rem;
  }
  .main-title {
    position: relative;
    width: 30rem;
    top: 10px;
  }
}
