.navigation {
  left: 92%;
  top: 200px;

  width: 70px;
  height: 400px;

  background: var(--trans);
  display: block;
  align-items: center;
  border-radius: 10px;
  position: fixed;
}
.navigation ul {
  display: table-row;
  width: 350px;
}
.navigation ul li {
  position: relative;
  list-style: none;
  width: 70px;
  height: 70px;
  z-index: 1;
}
.navigation ul li a {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
  text-align: center;
  font-weight: 500;
}
.navigation ul li a .icon {
  position: relative;
  display: block;
  line-height: 120px;
  font-size: 1.5em;
  text-align: center;
  transition: 0.5s;
  color: white;
}
.navigation ul li.active a .icon {
  transform: translateX(-55px);
}
.navigation ul li a .text {
  position: absolute;
  color: white;
  font-weight: 400;
  font-size: 0.75em;
  letter-spacing: 0.05em;
  transition: 0.5s;
  opacity: 0;
  transform: translateX(20px);
}
.navigation ul li.active a .text {
  opacity: 1;
  transform: translateX(4px);
}
.indicator {
  position: absolute;
  justify-content: right;
  top: 6%;
  right: 77%;
  width: 70px;
  height: 70px;
  background: var(--trans1);
  border-radius: 50%;
  transition: 0.5s;
  z-index: -1;
}
.indicator::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  top: -30px;
  left: 24px;
  background: transparent;
  border-radius: 50%;
  box-shadow: 15px 18px #fa20d2;
}
.indicator::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  bottom: -30px;
  left: 24px;
  background: transparent;
  border-radius: 50%;
  box-shadow: 16px -16px #fa20d2;
}
.navigation ul li:nth-child(1).active ~ .indicator {
  transform: translateY(calc(70px * 0));
}
.navigation ul li:nth-child(2).active ~ .indicator {
  transform: translateY(calc(70px * 1));
}
.navigation ul li:nth-child(3).active ~ .indicator {
  transform: translateY(calc(70px * 2));
}
.navigation ul li:nth-child(4).active ~ .indicator {
  transform: translateY(calc(70px * 3));
}
.navigation ul li:nth-child(5).active ~ .indicator {
  transform: translateY(calc(70px * 4));
}
@media screen and (max-width: 480px) {
  .navigation {
    display: none;
  }
}
