.header-content {
  font-family: "Poppins", sans-serif;
  font-size: 1.1rem;
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
}
/*header-content*/

.header-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-bottom: 5rem;
  padding-left: 2rem;
}
.header-content .left-header {
  display: flex;
  align-items: center;
  position: relative;
}

.header-content .right-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-right: 12rem;
}
.header-content .right-header .name {
  font-size: 3rem;
}
.header-content .right-header .name span {
  color: var(--trans1);
}
.header-content .right-header p {
  margin: 1.5rem 0;
  line-height: 2rem;
}

.btn-con {
  position: relative;
  top: 100px;
  left: 65px;
}
.main-btn {
  background: linear-gradient(180deg, #fa20d2 26.71%, #80106b 99.36%);
  box-shadow: 0px 18px 11px 1px rgba(196, 0, 174, 0.42);
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 16px;
  padding: 11px 18px 11px 18px;
}
.main-btn .btn-text {
  padding: 0 2rem;
}
.btn-con a {
  text-decoration: none;
}

.main-btn::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  transform: tanslateX(100%);
  transition: all 0.4s ease-out;
  z-index: -1;
}
.main-btn:hover {
  transition: all 0.4s ease-out;
  background: white;
  cursor: pointer;
  border: 1px solid var(--trans1);
  color: var(--trans1);
}

.header-content .left-header .image > :nth-child(2) {
  height: 100%;
  width: 95%;
  position: absolute;
  z-index: -2;
  top: -2em;
  left: -2em;
}
.header-content .left-header .image > :nth-child(3) {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  top: 70px;
  left: 5px;
}

.header-content .left-header .image > :nth-child(1) {
  position: relative;
  height: 80%;
  width: 80%;
  top: -13px;
  left: 50px;
}
.header-content .left-header {
  margin-top: 80px;
}
.header-content .right-header {
  margin-top: 100px;
}

.i-icon {
  position: absolute;
  left: 5em;
  margin-top: 45rem;
  transform: scale(0.47);
  display: flex;
  gap: 2rem;
}
.i-icon > *:hover {
  cursor: pointer;
  transform: scale(1.2);
}
@media screen and (max-width: 480px) {
  .header-content {
    display: flex;
    flex-direction: column;
    gap: 25rem;
  }

  .left-header {
    position: relative;
    width: 100%;
    margin: 3rem;
    bottom: 2rem;
    left: 5rem;
  }
  .Floating-div {
    position: relative;
    top: 4rem;
  }

  .Floatingdiv {
    position: relative;
    top: -21rem;
    right: 1rem;
    transform: scale(0.8);
  }
  .Floatingdiv > :nth-child(1) {
    position: absolute;
    padding-right: 1rem;
  }
  .Floatingdiv > :nth-child(2) {
    transform: scale(25%);
    position: absolute;
  }
  .Floatingdiv > :nth-child(4) {
    position: absolute;
    margin-left: -4rem;
  }
  .Floatingdiv > :nth-child(5) {
    position: absolute;
    margin-left: -5rem;
  }
  .Floatingdiv > :nth-child(6) {
    position: absolute;
    top: 15rem;
    margin-left: -4rem;
  }
  .header-content .left-header .image > :nth-child(1) {
    position: relative;
    top: 1rem;
  }
  .header-content .left-header .image > :nth-child(2) {
    position: absolute;
    top: 10px;
    left: 2px;
  }
  .main-btn {
    position: relative;
    left: 4rem;
  }
  .right-header {
    display: flex;
    width: 200%;
    padding: 10rem 2rem 4rem;
  }
  .right-header {
    position: relative;
    left: 5rem;
  }
  .i-icon {
    position: absolute;
    top: -15rem;
    right: 20rem;
  }
  .header-content .left-header .image {
    position: absolute;
    top: 1px;
    transform: scale(1.5);
  }
}
