.Floatingdiv > :nth-child(1) {
  transform: scale(0.3);
  position: absolute;
  top: 1em;
}
.Floatingdiv > :nth-child(2) {
  transform: scale(25%);
  position: absolute;
  top: 1em;
}
.Floatingdiv > :nth-child(3) {
  transform: scale(25%);
  position: absolute;
  left: 15rem;
  top: 8em;
}
.Floatingdiv > :nth-child(4) {
  transform: scale(0.3);
  position: absolute;
  left: -9em;
  top: 8em;
}
.Floatingdiv > :nth-child(5) {
  transform: scale(0.2);
  position: absolute;
  left: 9em;
  top: 12em;
}
.Floatingdiv > :nth-child(6) {
  transform: scale(0.2);
  position: absolute;
  left: 15rem;
  top: 16em;
}
