.main-project {
  padding-top: 8rem;
}

.port-text {
  padding: 2rem 0;
  text-align: center;
}
.portfolios {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  padding-left: 15rem;
  padding-right: 15rem;
}
.portfolios .portfolio-item {
  position: relative;
  border-radius: 10rem;
}
.portfolios .portfolio-item img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 15px;
}
.portfolios .portfolio-item .hover-items {
  width: 100%;
  height: 100%;
  background-color: var(--color-secondary);
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  transform: scale(0);
  transition: all 0.4s ease-in-out;
}
.portfolios .portfolio-item .hover-items h3 {
  font-size: 1.5rem;
  color: var(--color-white);
  margin-bottom: 1.5rem;
}
.portfolios .portfolio-item .hover-items .icons {
  display: flex;
  justify-content: center;
  align-items: center;
}
.portfolios .portfolio-item .hover-items .icons .icon {
  background-color: var(--color-primary);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 1rem;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}
.portfolios .portfolio-item .hover-items .icons .icon i {
  font-size: 1.5rem;
  color: var(--color-white);
  margin: 0 1rem;
}
.portfolios .portfolio-item .hover-items .icons .icon:hover {
  background-color: var(--color-white);
}
.portfolios .portfolio-item .hover-items .icons .icon:hover i {
  color: var(--color-primary);
}
.portfolio-item:hover .hover-items {
  opacity: 1;
  transform: scale(1);
}
.main-title {
  position: relative;
  left: 1rem;
  bottom: 5rem;
  font-size: 4rem;
  color: #ddd;
  text-align: center;
  font-weight: 700;
}
.main-title .port-text {
  font-size: 1rem;
  color: var(--gray);
}
@media screen and (max-width: 480px) {
  .main-project {
    margin-top: 30rem;
  }
  .main-project .portfolios {
    display: flex;
    flex-direction: column;
    width: 250%;
  }
  .main-project .portfolios {
    position: relative;
    top: 6rem;
    right: 7rem;
  }

  #tek {
    width: 30rem;
    top: 20px;
    left: 10rem;
  }
}
