.container {
  padding: 8rem 14rem 1rem;
}

/*Skills*/
.skills-container {
  position: relative;
  padding-left: 5rem;
  bottom: 5rem;
}
.skills-container .right-skills {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}
.skills-container .right-skills .skills-item {
  border: 1px solid var(--color-grey-5);
  background-color: var(--blueCard);
  width: 8rem;
  height: 9rem;
  border-radius: 100%;
  margin-top: 2rem;
  margin-bottom: 5rem;
}
#js {
  transform: scale(35%);
  right: 40px;
}
#bootstrap {
  transform: scale(26%);
  right: 7.6rem;
}
.skills-container .right-skills .skills-item:hover {
  cursor: default;
  transform: translateY(-5px);
  border: 2px solid var(--color-secondary);
  box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.32);
  background-color: rgba(255, 253, 253, 0.32);
}

.skills-container .right-skills .skills-item .large-text img {
  position: relative;

  right: 85px;
  bottom: 5rem;
  transform: scale(0.4);
}

.skills-container .right-skills .skills-item .abt-text {
  padding: 4rem;
  display: flex;
  flex-direction: column;
}

.skills-container .right-skills .skills-item .abt-text .small-text {
  padding-left: 3rem;
  position: relative;
  right: 80px;
  bottom: 14rem;
  font-size: 1.2rem;
  color: var(--color-grey-1);
  text-align: center;
}
.skills-container .right-skills .skills-item .abt-text .small-text span {
  font-size: 15px;
  color: var(--gray);
}
.skills-container .right-skills .skills-item .abt-text .small-text::before {
  content: "";
  position: absolute;
  left: 2rem;
  bottom: 18px;
  width: 6.5rem;
  height: 2px;
  background-color: var(--color-grey-5);
}
.title {
  position: relative;
  right: 2rem;
  bottom: 2rem;
}
.skills-container .right-skills .btn-con {
  position: relative;
  top: 10px;
  left: 19rem;
}
@media screen and (max-width: 480px) {
  .container {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    position: relative;
    bottom: 11rem;
    left: 4rem;
  }
  .title {
    width: 30rem;
    right: 10rem;
  }
  .skills-container {
    right: 14rem;
  }
  .skills-container .right-skills .btn-con {
    width: 10rem;
    position: relative;
    left: 8rem;
  }
}
