.footer {
  display: flex;
  align-items: center;
  margin: -1rem 0 -3rem;
  position: relative;
  overflow: hidden;
}
.f-content {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin-top: 3rem;
  gap: 4rem;
  color: white;
}

.f-content .lower {
  position: relative;
  top: 5rem;
  color: var(--color-grey-5);
}
.f-icon {
  position: relative;
  left: 39rem;
  bottom: 4.5rem;
  display: flex;
  gap: 1rem;
}
.f-icon a {
  font-size: 2rem;
  color: var(--color-white);
}
.f-icon a:hover {
  transform: scale(1.5);
}

@media screen and (max-width: 480px) {
  .f-content {
    transform: scale(0.5);
  }
 .footer {
    position: relative;
    margin-top: 30rem;
    width: 200%;
  }
  .f-icon {
    position: relative;
    bottom: 22px;
    left: 20rem;
  }
}
